.DisplayCards {
    border: 1px solid #fff;
    cursor: pointer;
    display: inline-block;
    vertical-align: top;
    
    box-sizing: border-box;
}

.ImageWrapper {
    position: relative;
}



.ImageWrapper::after {
    content: "";
    position: absolute;
    width: 100%;
    height:100%;
    background: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
    display: none;
}

.DetailsWrapper {
    

}

.DisplayContent {
    /* width : 1717px; */
}

.Info1 {
    display: grid;
    grid-gap: 2px;
    grid-template-columns: repeat(2, 1fr);
    font-size: 12px;
    padding: 0px 18px;
}

.ReleaseDate {
    justify-self: start;
}

.AvgVote {
    justify-self: end;
}

.ReleaseDate , .AvgVote {
    font-weight: bold;
}

@media only screen and (max-width: 500px) {
    .ImageWrapper img {
        max-width: 100%;
    }

    .DisplayCards {
        width:100%;
    }
  }